<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">发起审批</div>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/home/investProjectManageHomepage' ,query: {id:projectId}}">返回项目首页</el-breadcrumb-item>
              <el-breadcrumb-item :to="{ path: '/home/applyList' ,query: {projectId:projectId,from:2}}">审批列表</el-breadcrumb-item>
              <el-breadcrumb-item>发起审批</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="line"></div>
        </div>
        <div class="createapply-home-icongroup" v-for="item in applyTypelist">
          <div class="createapply-home-icongroup-title">
            {{item.name}}
          </div>
          <div class="createapply-home-icon-itemlist">
            <div class="createapply-home-iconitem" @click="gotoCreateApply(item1)" :class="{'createapply-home-iconitem-disabled':item1.disabled}" v-for="item1 in item.child">
              <img :src="item1.icon" class="">
              <div>{{item1.name}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//审批流程状态
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
import { getApplyType } from '../../config/api'
export default {
  data() {
    return {
      applyTypelist: [{
        name: '出勤休假',
        child: []
      }, {
        name: '用印申请',
        child: []
      }, {
        name: '签字申请',
        child: []
      }],
      disabledIdlist: []
    }
  },
  created() {
    if (!this.$route.query.projectId) {
      this.projectId = '';
      this.$router.push({
        name: 'investProjectManageList'
      })
    } else {
      this.projectId = this.$route.query.projectId
    }
    //获取审批类型
    getApplyType({
      type: '2,3'
    }).then(res => {
      if (res.data.msgNo == 1) {
        let tl = res.data.payload;
        tl.forEach(item => {
          if (this.disabledIdlist.indexOf(item.id) != -1) {
            item.disabled = true;
          } else {
            item.disabled = false;
          }
          item.icon = '../../static/images' + item.icon;
          this.applyTypelist[parseInt(item.type) - 1].child.push(item)
        })
        this.applyTypelist.forEach((item, index) => {
          if (item.child.length <= 0) {
            this.applyTypelist.splice(index, 1);
          }
        });
      }
    })
  },
  components: {},
  computed: {},
  mounted() {},
  methods: {
    gotoCreateApply(item) {
      this.$router.push({
        name: 'createApply',
        query: {
          template_id: item.id,
          aType: item.type,
          aName: item.name,
          from: 2,
          projectId: this.projectId
        }
      })
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.historyapply-group {
  width: 100%;
  border: 1px solid #E4E4E4;
  padding: 18px 20px 16px;
}

.table-group-title {
  padding-left: 0px;
}

</style>



